<template>
  <div>
    <a-spin :spinning="spinning">
      <div style="text-align: left">
        <a-card title="" :bordered="false" size="small">
          <template>
            <a-form-model layout="inline" :model="formsource">
              <a-form-model-item label="">
                <a-select
                  v-model="resourceUrl"
                  show-search
                  placeholder=""
                  option-filter-prop="children"
                  style="width: 750px"
                  :filter-option="filterOption"
                  @focus="handleFocus"
                  @blur="handleBlur"
                  @change="handleChange"
                >
                  <a-select-option v-for="item in resourceLis" :key="item.data_id" :value="item.data_value">
                    <a-icon type="close-circle" @click="delData(item.data_id)"/>
                    <a-tag color="#2db7f5">{{ item.data_desc }}</a-tag>
                    {{ item.data_value }}
                  </a-select-option>
                </a-select>
              </a-form-model-item>
              <a-form-model-item style="float: right">
                <a-button
                  type="primary"
                  html-type="submit"
                  @click="showModal"
                >
                  
                </a-button>
                <a-button
                  type="danger"
                  @click="post"
                >
                  
                </a-button>
                <a-button @click="showModal2"></a-button>
              </a-form-model-item>
            </a-form-model>
          </template>

          <!--        -->
          <template>
            <div>
              <a-modal v-model="visible" title="">
                <template slot="footer">
                  <a-button key="ok" type="danger" @click="handleOk">
                    
                  </a-button>
                </template>
                <template>
                  <a-form-model layout="inline" :model="formInline">
                    <a-form-model-item label="">
                      <a-input
                        v-model="formInline.data_value"
                        default-value="http://"
                        placeholder=""
                        style="width: 350px">
                      </a-input>
                    </a-form-model-item>
                    <a-form-model-item label="">
                      <a-input v-model="formInline.data_desc" placeholder="" style="width: 350px">
                      </a-input>
                    </a-form-model-item>
                  </a-form-model>
                </template>

              </a-modal>
            </div>
          </template>
        </a-card>
        <!---->
        <template>
          <div>
            <a-modal v-model="visible2" title="">
              <template slot="footer">
                <a-button key="back" @click="closeModal">
                  
                </a-button>
              </template>
              <a-input
                v-model="token"
                placeholder=""
              />
            </a-modal>
          </div>
        </template>

        <a-card>

          <a-textarea
            v-model="kzt"
            placeholder=""
            :row="10"
            :minRows="10"
            style="height: 200px"
            @change="kztChange"
            @select="testSelect"/>
          <a-tag color="#f50">SQL:</a-tag>
          {{ txt }}
        </a-card>
        <a-card>
          <template slot="title">
            <h3>
              <a-button key="export" type="primary" @click="exportExcel"></a-button>
            </h3>

          </template>
          <template>

            <a-table :columns="columns" :data-source="data" :scroll="{ x: 1500, y: 300 }" bordered>
              <p slot="expandedRowRender" slot-scope="record" style="margin: 0">
                {{ record.description }}
              </p>
            </a-table>
          </template>
        </a-card>

      </div>
    </a-spin>
  </div>
</template>
<script>
import {ipcApiRoute} from "@/api/main"
import ExportExcel from "@/utils/exportExcel";

const columns = [
  {title: 'Full Name', width: 100, dataIndex: 'name', key: 'name', fixed: 'left'},
  {title: 'Age', width: 100, dataIndex: 'age', key: 'age', fixed: 'left'},
  {title: 'Column 1', dataIndex: 'address', key: '1'},
  {title: 'Column 2', dataIndex: 'address', key: '2'},
  {title: 'Column 3', dataIndex: 'address', key: '3'},
  {title: 'Column 4', dataIndex: 'address', key: '4'},
  {title: 'Column 5', dataIndex: 'address', key: '5'},
  {title: 'Column 6', dataIndex: 'address', key: '6'},
  {title: 'Column 7', dataIndex: 'address', key: '7'},
  {title: 'Column 8', dataIndex: 'address', key: '8'},
];

const data = [
  {
    key: '1',
    name: '',
    age: 32,
    address: '',
  }
];

export default {
  data() {
    return {
      spinning: false,
      formsource: {},
      visible2: false,
      token: '',
      data,
      columns,
      kzt: '',
      txt: '',
      isUpdate: false,
      resourceLis: [],
      resourceUrl: '',
      visible: false,
      formInline: {
        data_value: 'http://',
        data_desc: '',
      },
    };
  },
  created() {
    this.initData()
    this.initScene()
  },
  methods: {
    initData() {
      const self = this;
      const params = {
        action: 'getDataConfigByKey',
        info: {
          data_key: 'MYSQL_API_URL'
        }
      }
      this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
        if (res.result.length == 0) {
          self.$message.error(``);
          return;
        }
        this.resourceLis = res.result
        self.$message.success(``);
      })
    },
    initScene() {//
      const self = this;
      const params = {
        action: 'getSceneTemp',
        info: {
          scene: 'mysqlTool'
        }
      }
      this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
        if (res.result.length === 0) {
          this.isUpdate = false
          return;
        }
        let ressource = eval('(' + res.result[0].alldata + ')')
        this.resourceUrl = ressource.resourceUrl
        this.kzt = ressource.kzt
        this.isUpdate = true
        self.$message.success(``);
      })
    },
    kztChange(e) {
      const kzt = this.kzt
      const resourceUrl = this.resourceUrl
      let info = {
        scene: "mysqlTool",
        memo: 'mysql',
        alldata: JSON.stringify({kzt: kzt, resourceUrl: resourceUrl})
      }
      if (this.isUpdate) {//
        const params = {
          action: 'updateSceneTemp',
          info: info
        }
        this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
          if (res.result) {
            this.isUpdate = true
            return;
          }
        })
      } else {//
        const params = {
          action: 'addSceneTemp',
          info: info
        }
        this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
          if (res.result) {
            this.isUpdate = true
            return;
          }

        })
      }

    },
    exportExcel() {
      ExportExcel(this.columns, this.data, Date.parse(new Date()) + '.xlsx');
    },
    delData(data_id) {
      const self = this;
      const params = {
        action: 'delDataConfig',
        info: {
          data_id: data_id
        }
      }
      this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
        if (res.result) {
          self.$message.success(``);
          this.initData()
          this.resourceUrl = ''
          return;
        }
        self.$message.success(``);
      })
    },
    testSelect(e) {
      let start = e.target.selectionStart;
      let end = e.target.selectionEnd;
      let value = e.target.value;
      console.log(e.target.selectionStart, e.target.selectionEnd, e.target.value);

      let selectVal = "";
      if (value && end != 0) { // 
        selectVal = value.slice(start, end);
      }
      console.log(selectVal)
      this.txt = selectVal;
    },
    showModal() {
      this.formInline = {
        data_value: 'http://',
        data_desc: '',
      }
      this.visible = true;
    },
    closeModal() {
      this.visible2 = false;
      this.visible = false;
    },
    showModal2() {
      this.visible2 = true;
    },
    handleOk(e) {
      console.log(e);
      this.handleSubmit(e)
      this.visible = false;
    },
    handleSubmit(e) {
      console.log(this.formInline);
      if (!this.formInline.data_desc) {
        return
      }
      const self = this;
      const params = {
        action: 'addDataConfig',
        info: {
          ...this.formInline,
          data_key: 'MYSQL_API_URL',
          data_memo: 'mysql',
        }
      }
      this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
        if (res.result) {
          self.$message.success(``);
          this.initData()
          return;
        }
        self.$message.success(``);
      })
    },
    post() {
      this.spinning = true
      const self = this;
      const params = {
        action: 'POST',
        data: {
          url: this.resourceUrl,
          sqlStr: this.txt,
          token: this.token,
        }
      }
      this.$ipcInvoke(ipcApiRoute.dataConfigOperation, params).then(res => {
        if (res.result.code === 0) {
          self.$message.success(``);
          this.spinning = false
          this.columns = []
          const columnall = []
          let data1 = res.result.data;
          let data1Element = {}
          if (data1) {
            data1Element = data1[0];
            for (let key of Object.keys(data1Element)) {
              console.log(key);
              let column = {
                title: key,
                dataIndex: key,
                ellipsis: true
              }
              this.columns.push(column)
            }
            this.columns.push({title: 'description', dataIndex: 'description', ellipsis: true})

            const finalData = []
            for (let i = 0; i < data1.length; i++) {
              data1[i].description = JSON.stringify(data1[i])
              finalData.push(data1[i])
            }
            this.data = finalData
          }
          return;
        }
        this.spinning = false
        self.$message.error(res.result.message);
      })
    },
    handleChange(value) {
      console.log(`selected ${value}`);
    },
    handleBlur() {
      console.log('blur');
    },
    handleFocus() {
      console.log('focus');
    },
    filterOption(input, option) {
      return (
          option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
      );
    },
  },
};
</script>

